import ReaderData from "../data/mock.json";

console.log(ReaderData, "ReaderInfo");

// 获取章节列表数据
export const getChapterList = ({ novelId }) => {
  return new Promise((resolve) => {
    let data = {};

    const chapterTitleList = ReaderData.chapterList.map((item) => {
      return {
        chapterTitle: item.chapterTitle,
        chapterId: item.chapterId,
      };
    });

    data.chapterList = chapterTitleList;
    data.novelId = Number(novelId);
    data.novelTitle = ReaderData.novelTitle;
    setTimeout(() => {
      resolve({
        data,
      });
    }, 500);
  });
};

// 获取章节信息
export const getChapterInfo = ({ novelId, chapterId }) => {
  return new Promise((resolve) => {
    let data = {};

    const chapterList = ReaderData.chapterList;

    data.novelId = Number(novelId);
    data.novelTitle = ReaderData.novelTitle;
    data.startChapterId = ReaderData.startChapterId;
    data.endChapterId = ReaderData.endChapterId;
    data = Object.assign({}, data, chapterList[chapterId - 1]);

    setTimeout(() => {
      resolve({
        data,
      });
    }, 500);
  });
};
